@import './resets';
@import './typography';
@import './variables';

.container {
  max-width: 450px;
  margin: 0 auto;
  border-right: 1px solid black;
  border-left: 1px solid $grey;

  video {
    width: 100%;
    height: 236px;
    object-fit: cover;
  } 
  
  article {
    margin: $marginV 5%;
    text-align: center;
    // margin: 0 5%;
  }

  .link-wrap {
    display: flex;
    justify-content: space-between;
    
    a {
      margin: 35px 0 $marginV 0;
      text-decoration: none;
      display: block;
      cursor: pointer;
    }
  }

  #twin-pics {
    display: flex;
    margin-bottom: $marginV;
  }

  .pic1 {
    background-image: url(../media/bamboo.jpg);
    width: 225px;
    height: 282px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .pic2 {
    background-image: url(../media/karateko.jpg);
    width: 225px;
    height: 282px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  #philo-quote {
    margin: calc(#{$marginV}*3) 15% calc(#{$marginV}*3) 15%;
  }

  .quote-design-flex {
    display: flex;
    align-items: center;
  }

  #opening-quote {
    height: 27px;
    width: 23px;
    background: url(../media/opening-quotes.JPG) no-repeat;
    background-size: contain;
    background-position: center;
    margin-right: 10px;
    // border: 1px red solid;
  }

  .quote-line {
    background-color: $light-grey;
    height: 1px;
    width: 100%;
    // border: 1px red solid;
    display: block;

  }

  
  #closing-quote {
    height: 27px;
    width: 23px;
    background: url(../media/closing-quotes.JPG) no-repeat;
    background-size: contain;
    background-position: center;
    margin-left: 10px;
  }

  #pattern {
    height: 84px;
    width: 100%;
    background: url(../media/texture-dark.png), linear-gradient(337deg, rgba(0,0,0,1) 0%, rgb(80, 80, 80) 100%);
    background-blend-mode: screen;
  }

}

